<template>
  <div class="container">
    <div class="post">
      
      <h2 class="title">欢迎注册！</h2>
      <div class="post_contain">
      <el-form
        :label-position="labelPosition"
        label-width="80px"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
      >
        <el-form-item label="昵称:" prop="name">
          <el-input v-model="ruleForm.name" ></el-input>
        </el-form-item>
        <el-form-item label="手机号:" prop="number">
          <el-input v-model="ruleForm.number"></el-input>
        </el-form-item>
        <el-form-item label="邮箱:" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="年龄:" prop="age">
          <el-input v-model="ruleForm.age" type="number"></el-input>
        </el-form-item>
         <el-form-item label="性别:" prop="gender">
         <el-radio-group v-model="ruleForm.gender">
            <el-radio label="男" value="1" ></el-radio>
            <el-radio label="女" value="0"></el-radio>
         </el-radio-group>
        </el-form-item>
        <el-form-item  label="密码:" prop="password" autocomplete="off">
          <el-input v-model="ruleForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="pwcheck" autocomplete="off">
          <el-input v-model="ruleForm.pwcheck" type="password"></el-input>
        </el-form-item>
        <el-link class="post_link" href="#/login">已有账号?点击进入登入界面</el-link>
        <el-form-item class="post_button">
          <el-button type="primary" @click="submitForm('ruleForm')">注册并登入</el-button>
        </el-form-item>
      </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
        if (value ===null) {
          callback(new Error('请输入密码'));
        } 
          callback();
        
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === null) {
          callback(new Error('请再次输入密码'));
        } else if (value!== this.ruleForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        labelPosition: 'right',
        ruleForm: {
          name: '',
          number: '',
          email: '',
          age:"",
          gender:"",
          password:"",
          pwcheck:""
        },
        rules:{
          name:[
            {required:true,message:'请输入昵称',trigger:'blur'},
            {min:1,max:10,message:'长度在1到5个字符',trigger:'blur'}
          ],
          number:[
             {required:true,message:'请输入手机号',trigger:'blur'},
            {min:11,max:11,message:'请输入正确的手机号',trigger:'blur'}
          ],
          email:[
            {required:true,message:'请输入邮箱',trigger:'blur'},
            {type:'email',message:'请输入正确的邮箱',trigger:'blur'}
          ],
          age:[
             {required:true,message:'请输入年龄',trigger:'blur'},
             
          ],
          gender:[
            {required:true,message:'请选择性别',trigger:'change'}
          ],
          password:[
            {validator:validatePass,trigger:'blur'}
          ],
          pwcheck:[
            {validator:validatePass2,trigger:'blur'}
          ]
        }
      };
    },
  methods:{
     submitForm(formName) {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
           let res=await  this.$http.post('/api/user')
           console.log(res);
           let User=res.data.User;
           //找到表中已经被注册，重复的号码
           let sameNumber=User.find(item=>item.number===this.ruleForm.number)
           //找到表中已经被注册，重复的邮箱
           let sameEmail=User.find(item=>item.email===this.ruleForm.email)
           //找到表中已经被注册，重复的昵称
           let sameName=User.find(item=>item.name===this.ruleForm.name)
           if(!sameNumber){
             if(!sameEmail){
               if(!sameName){
               this.$http.post('/api/register',this.ruleForm);
               this.$http.post('/api/login',this.ruleForm);
               
                 alert("注册成功！");
                 
                 this.$router.push({
                   path:'/'
                 })
               }
               else{
                 alert("注册失败，此昵称以被注册过")
               }
               
             }
             else{
               alert("注册失败，此邮箱已被注册过！")
             }
           }
           else{
             alert("注册失败，此号码已被注册过！")
           }
            // await this.$http.post('/api/register',this.ruleForm)
           
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
  }
};
</script>

<style scoped>
.post_link{
      margin-left: 135px;
}
.post_button{
  margin-top: 20px;
  margin-left: 90px;
}
.post_contain{
  width: 70%;
  height: 100%;
  margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;
    margin-left: auto;
}
.container {
  height: 712px;
  width: 100%;
  background: url("../../public/image/login_bg.jpg");
  background-repeat: no-repeat;
}
.post {
  margin: 0 auto;
  height: 600px;
  width: 600px;
  border-radius: 10px;
  background-color: aliceblue;
}
.title {
  margin-left: 20px;
  margin-top: 50px;
  color: #424242;
  font-size: 32px;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}
</style>
