<template>
   <div class="container">
    <div class="post">
      
      <h2 class="title">欢迎登入！</h2>
      <div class="post_contain">
      <el-form
        :label-position="labelPosition"
        label-width="80px"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
      >
        
        <el-form-item label="用户名:">
          <el-input v-model="ruleForm.number" placeholder="手机号" prop="number"></el-input>
        </el-form-item>
        
        <el-form-item label="密码:">
          <el-input type="password" v-model="ruleForm.password" placeholder="密码" prop="password"></el-input>
        </el-form-item>
        
        <el-link class="post_link" href="#/register">没有账号?点击注册</el-link>
        <el-form-item class="post_button">
          <el-button type="primary" @click="submitForm('ruleForm')">登入</el-button>
        </el-form-item>
      </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { setStore } from '@/utils/storage';
export default {
  
    data() {
      return {
        labelPosition: 'right',
        ruleForm: {
          number: '',
          password:''
        },
        rules:{
          number:[
             {required:true,message:'请输入手机号',trigger:'blur'}
          ],
          password:[
             {required:true,message:'密码',trigger:'blur'}
          ]
        }
      };
    },
  methods:{
     submitForm(formName) {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            //获取用户名和密码
           let res= await this.$http.post('http://10.128.254.109:3000/api/login',this.ruleForm)
           
           if(res.data.code==200){
             let userInfo=res.data.user;
            
             console.log(userInfo);
            //持久化 存储
            setStore('token',res.data.token);
            // setStore('id',userInfo.id);
            // setStore('name',userInfo.name);
            // setStore('headImgUrl',userInfo.headImgUrl);
             alert("登入成功！")
             this.$store.commit('ISLOGIN',userInfo);
              this.$router.push({
               path:'/'
             })
           }
           else{
             alert("用户名或密码错误！");
           }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
  },
  
  }

</script>

<style scoped>
.post_link{
      margin-left: 135px;
}
.post_button{
  margin-top: 20px;
  margin-left: 90px;
}
.post_contain{
  width: 70%;
  height: 100%;
  margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;
    margin-left: auto;
}
.container {
  height: 712px;
  width: 100%;
  background: url("../../public/image/login_bg.jpg");
  background-repeat: no-repeat;
}
.post {
  margin: 0 auto;
  height: 330px;
  width: 600px;
  border-radius: 10px;
  background-color: aliceblue;
}
.title {
  margin-left: 20px;
  margin-top: 50px;
  color: #424242;
  font-size: 32px;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}
</style>