import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/views/Index';
import Login from '@/views/Login';
import Home from '@/views/Home';
import Register from '@/views/Register';
import Article from '@/views/Article';
import File from '@/views/File';
import Tags from '@/views/Tags';
import Message from '@/views/Message';
import Administer from "@/views/Administer";

Vue.use(Router);

const routes=[
    {
        path:"/",
        redirect:"/Home",
        name:"home",
        meta:{
            title:'守夜人的博客'
        },
        component:Index,
        children:[
            {
                path:"home",
                component:Home,
                meta:{
                    title:'守夜人的博客'
                },
            } 
        ]
    },
    {
        path:"/login",
        name:"login",
        component:Login,
        meta:{
            title:'守夜人的博客'
        },
    },
    {
        path:"/register",
        name:"register",
        component:Register,
        meta:{
            title:'守夜人的博客'
        },
        
    },
    {
        path:'/article/:id',
        
        component:Article,
        meta:{
            title:'守夜人的博客'
        },
        
    },{
        path:'/file',
        name:"file",
        component:File,
        meta:{
            title:'守夜人的博客'
        },
    },
    {
        path:'/tags',
        name:"tags",
        component:Tags,
        meta:{
            title:'守夜人的博客'
        },
    },
    {
        path:"/message",
        name:"message",
        component:Message,
        meta:{
            title:'守夜人的博客'
        },
    },
    {
        path:"/administer",
        name:"administer",
        component:Administer,
        meta:{
            title:'守夜人的博客'
        },
    }
]


const router=new Router({
    routes //(缩写)相当于routes:routes
})

export default router;