<template>
  <div class="container">
    <el-divider></el-divider>
    <el-row>
  <el-col :span="8"   class="card" v-for="(article,index) in articles" :key="index">
    <el-link :underline="false"  @click="checkArticle(article._id)"><el-card  class="article_card">
      <el-link :underline="false"   @click="checkArticle(article._id)"><img :src="imageUrl" class="image"  @mouseover.stop="Mouseover(index)" @mouseleave.stop="Mouseleave(index)" ></el-link>
      
     <div class="card_title" v-show="article.titleIsShow"  @mouseover.stop="Mouseover(index)" @mouseleave.stop="Mouseleave(index)" >
        <h2 class="card_title_item">{{article.title}}</h2>
      </div>
    
      
      <div class="card_hover"  v-show="article.shadowIsShow"  @mouseover.stop="Mouseover(index)" @mouseleave.stop="Mouseleave(index)">
        <span class="el-icon-view icon_eye"></span>
      </div>
   
<div class="introduce" v-show="true">
        <span class="article_date" v-show="true">
          {{article.date}}
        </span>
        <el-link :underline="false"  @click="checkArticle(article._id)"><div class="article_text">
          <p>
            {{article.introduce}}
          </p>
        </div></el-link>
      </div>
      <el-divider class="text_line"></el-divider>
      <div class="footer">
        <el-button type="primary" plain class="footer_label" >{{article.type}}</el-button>
      </div>
    </el-card>
    </el-link>
  </el-col>
</el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imageUrl: "http://10.128.254.109:3000/img/31.jpg",
      currentDate: new Date(),
      shadowIsShow:false,
      titleIsShow:true,
      articles:[],

    };
  },
   async created(){
     try{
       const res=await this.$http.get('http://10.128.254.109:3000/api/articles');
       
       let data=res.data;
       for(let i=0;i<data.length;i++){
         data[i].titleIsShow=true;
         data[i].shadowIsShow=false;
       }
       this.articles=data;
       

     }
     catch(err){
       console.log(err.message);
     }
  },
  
  methods:{
    checkArticle(id){
      
      this.$router.push({
        path:'/article/'+id
      })
    },
    Mouseover(index){
      this.articles[index].shadowIsShow=true;
      this.articles[index].titleIsShow=false
    },
    Mouseleave(index){
      this.articles[index].shadowIsShow=false;
      this.articles[index].titleIsShow=true
    }
  }
};
</script>

<style scoped>
.container {
  margin: 0 auto;
  height: auto;
  width: 80%;
  
}
.article_card{
  margin-top:20px;
  height: 427px;
  width: 520px;
}
.article_card:hover{
  box-shadow: darkgrey 5px 5px 15px 5px ;
}
.card{
  position: relative;
    /* padding: 0px; */
    padding-left: 45px;
    display: inline-table;
}
.card_title{
   position: absolute;
   z-index: 1;
   height: 50px;
   width: 480px;
   background-color:black;
   opacity: 0.5;
   top: 190px;
   text-align: center;
   
}
.card_title h2{
  color: aliceblue;
}
.card_title_item{
  position: relative;
  top: 20%;
}
  .image {
    width: 100%;
    display: block;
    height: 260px;
    z-index: 0;
  }
  .card_hover{
    background-color: black;
    opacity: 0.5;
    width: 100%;
    height: 260px;
    position: relative;
    top: -259px;
    font-size: 50px;
    color: aliceblue;

  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
  .icon_eye{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);

  }
  .introduce{
    display: block;
    position: absolute;
    top: 250px;
    height: auto;
    width: 480px;
  }
  .article_date{
    text-align: center;
    position: relative;
    left: 190px;
    color: #666;
    font-size: 12px;
  }
  .article_text{
    color: #555;
    font-size: 15px;
    line-height: 1.8;
    margin-top: 100px;
    margin-left: -90px;

  }
  .text_line{
    position: absolute;
    bottom: 40px;
    max-width: 480px;
  }
  .footer{
    position: absolute;
    width: 480px;
    height: auto;
    bottom: 15px;
  }
  .footer_label{
    position: relative;
  }


</style>
