<template>
  <div class="container">
      <div class="contain_header">
          
      </div>
      
      <m-header id="myHeader" ></m-header>
     
      <div class="title">
          <h2 class="title_item">TAGS</h2>
          <ul class="item_list" >
              <li class="list_item " :class="activeClass==index? 'list_item_active':''"  @click="listClick(index)" v-for="(list,index) in lists" :key="index">
                  <a href="#">{{list.name}}</a>
                  </li>
                  
          </ul>
          
      </div>
      <div class="contain">
          <h3 class="archive-separator">{{typeNow}}</h3>
          
          <div><el-card shadow="hover" class="box-card" v-for="(article,index) in articles" :key="index" v-if="article.type==typeNow" >
            <div slot="header" class="clearfix">
               <div class="post-mate">
                   <time class="post-time">{{article.date}}</time>
                   <h3 class="post-title">
                       <el-link :underline="false" class="post-title-link"  href="#/article">
                           {{article.title}}
                       </el-link>
                   </h3>
               </div>
               
    
            </div>
            <el-row>
                     <el-button @click="IconClick(article.type)"  type="primary" plain>{{article.type}}</el-button>
           </el-row>

           </el-card></div>
          
           
          
             <el-card shadow="hover" class="box-card" v-for="(article,index) in articles" :key="index" v-if="typeNow==sum" >
            <div slot="header" class="clearfix">
               <div class="post-mate">
                   <time class="post-time">{{article.date}}</time>
                   <h3 class="post-title">
                       <el-link :underline="false" class="post-title-link"   @click="checkArticle(article._id)">
                           {{article.title}}
                       </el-link>
                   </h3>
               </div>
               
    
            </div>
            <el-row>
                     <el-button @click="IconClick(article.type)"  type="primary" plain>{{article.type}}</el-button>
           </el-row>

           </el-card>
          

      </div>
      <MFooter></MFooter>
  </div>
</template>

<script>
import MHeader from '@/commond/MHeader';
import MFooter from '@/commond/MFooter';
export default {
components:{
    MHeader:MHeader,
    MFooter:MFooter
},
data(){
    return{
      
      
      
      articles:[],
      lists:[],
      activeClass:0,
      typeNow:'全部',
      cardIsShow:true,
      sum:'全部'
    }
}
,
  methods:{
      checkArticle(id){
      
      this.$router.push({
        path:'/article/'+id
      })
    },
    IconClick(type){
      this.typeNow=type;
    },
    
    listClick(index){
        this.activeClass=index;
        this.typeNow=this.lists[index].name;
        var i ,j;
        for(i=0;i<=this.articles.length;i++){
          let typelist=this.articles[i].types;
          for(j=0;j<=typelist.length;j++){
            if(this.lists[index].title==typelist[j]){
              this.cardIsShow=true;
            }
            else{
              this.cardIsShow=false;
            }
          }
        }

    }
  },
  async created(){
   document.documentElement.scrollTop=0;
     try{
       const res=await this.$http.get('http://10.128.254.109:3000/api/articles');
       let data=res.data;
        this.articles=data;

        this.$http.get('http://10.128.254.109:3000/api/Tags').then(
            res=>{
                this.lists=res.data;
            }
        )
     }
     catch(err){
       console.log(err.message);
     }
  },
 
}
</script>

<style scoped>
.post-title-link {
    color: #424242;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
    position: relative;
    display: inline-block;
    font-weight: bold;
    font-size: 22px;
}
.archive-article .post-title {
    font-size: 18px;
    margin-bottom: 0;
    padding-bottom: 16px;
    text-align: left;
    position: relative;
    left: 0;
    top: 0;
    background: initial;
    line-height: 32px;
    text-overflow: initial;
    overflow: initial;
    white-space: initial;
}
.article-card .post-time {
    line-height: 24px;
    font-size: 11px;
    
}
.post-meta {
    text-align: center;
    margin: 0 0 10px;
    line-height: 14px;
    
    color: #666;
    position: relative;
    z-index: 1;
    overflow: hidden;
}


  

  .box-card {
        width: 560px;
    display: inline-block;
    margin: 10px;
  }
h3{
    line-height: 34px;
}
.archive-separator:first-child{
    margin: 10px;
}
.archive-separator {
    margin: 35px 0 13px;
    color: #424242;
    font-size: 20px;
}
.contain{
    height: auto;
    width: 80%;
    
    margin: 0 auto;
}
.list_item_active{
border-bottom: 2px red solid;
}
.item_list{
    position: absolute;
        left: -520px;
    top: 115px;

}
.list_item{
    height: 30px;
    width: auto;
    list-style: none;
    display: inline-block;
    margin: 10px;
    font-weight: bold;
    text-align: center;
}

.list_item a{
text-decoration: none;
color: #dbd5d5;
}
.title_item{
    font-weight: bold;
    text-shadow: 2px 2px 10px rgba(0,0,0,0.6);
    font: 700 40px/50px Tahoma;
    letter-spacing: 1.1px;
    font-size: 50px;
}
.title{
   
    color: #dbd5d5;
    position: absolute;
    left: 50%;
    top: 160px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
