<template>
  <div class="container">
      <div class="contain_header">
          
      </div>
      <transition appear
                    enter-class="a1"
                    enter-active-class="b1"
                    enter-to-class="c1"
                    leave-class="aa1"
                    leave-active-class="bb1"
                    leave-to-class="cc1"
                    >
      <m-header id="myHeader" v-show="Header_IsShow"></m-header>
      </transition>
      <!-- 文章 -->
      <div class="post_body">
        <!-- 卡片头部 -->
          <div class="post_card">
              <h1 class="post_card_title">{{article.title}}</h1>
              <div class="post_meta">
                  <time class="post_time">{{article.date}}</time>
              </div>
              <!-- 卡片内容 -->
              <div class="post_content" >
                <p v-html="article.content"></p>
              </div>
              <!-- 卡片底部 -->
              <div class="post_footer">
                <div class="post_link">
                  <span>
                    最后更新时间：
                    <time class="post_time">{{article.date}}</time>
                  </span>
                  <br>
                  
                  原文地址：
                  
                  <a href="http://localhost:8080/#/article/article._id" target="_blcnk">http://localhost:8080/#/article/{{article._id}}</a>
                  <br>
                  
                  原作者：
                  
                  <a href="http://localhost:8080/#/home" target="_blcnk">{{article.Author}}</a>
                  
                </div>
                <div class="post_author">
                  <el-link :underline="false" href="/">
                    <el-avatar shape="circle" :size="40" :src="squareUrl" class="my_head"></el-avatar>&nbsp;
                    <span class="author_name">守夜人</span>
                  </el-link>
                </div>
                <el-divider class="text_line"></el-divider>
                <div class="article_label">
                   <ul>
                     <li class="label_item" @click="tagClick()"><el-button type="primary" plain>{{article.type}}</el-button></li>
                     
                   </ul>
          </div>
              </div>
              
          </div>
          
          

      </div>
      <div class="page_change">
        <div class="page_change_left page_change_item">
          <div class="prev"><span class="el-icon-arrow-left"></span>Prev</div>
          <el-link :underline="false" :disabled="prevIsDisabled" @click="checkArticle(prev._id)"><h2 class="page_title">{{prev.title}}</h2></el-link>
        </div>
        <div class="page_change_right page_change_item">
          <div class="next"><span class="el-icon-arrow-right"></span>Next</div>
          <el-link :underline="false" :disabled="nextIsDisabled" @click="checkArticle(next._id)"><h2 class="page_title">{{next.title}}</h2></el-link>
        </div>
      </div>
      <MFooter></MFooter>
  </div>
</template>

<script>
import MHeader from '@/commond/MHeader';
import MFooter from '@/commond/MFooter';
import VueMarkdown from 'vue-markdown';
export default {
components:{
    MHeader:MHeader,
    MFooter:MFooter,
    VueMarkdown:VueMarkdown
  },
   data(){
    return {
      squareUrl:"http://10.128.254.109:3000/img/my_head.jpg",
      // Header_Icon_IsShow:false,
      Header_IsShow:false,
      // Onetimer:'',
      Twotimer:'',
      article:{},
      prev:{},
      next:{},
      prevIsDisabled:false,
      nextIsDisabled:false,
      
    }
  }
  ,
  methods:{
    tagClick(){
      this.$router.push({
        path:"/tags"
      })
    },
    //导航隐藏和显示动画
    Twotoggle(){
      this.Header_IsShow=!this.Header_IsShow;
    },
    check_Twotoggle(){
      if(document.documentElement.scrollTop<240){
        this.Header_IsShow=false;
      }
      else{
        this.Header_IsShow=true;
      }
    },
    getArticle(){
      this.$http.post('http://10.128.254.109:3000/api/article',{id:this.$route.params.id}).then(
         res=>{
           console.log(res.data);
           this.article=res.data.article;
          if(res.data.prev==null){
            this.prev={
              title:"上一篇没有了",
              _id:200
            };
            this.next=res.data.next;
            this.prevIsDisabled=true;
          } else {
            this.prev=res.data.prev;
            console.log(res.data.prev);
            this.next={
              title:"下一篇没有了",
              _id:200
            };
            this.nextIsDisabled=true;
          }                                                         
           
         }
       )
    },
    checkArticle(id){
this.$router.push({
        path:'/article/'+id
      });
      document.documentElement.scrollTop=0;
      this.getArticle();
    },
  },
  async created(){
    document.documentElement.scrollTop=0;
     try{
       console.log(this.$route.params.id);
       this.getArticle();
       
     }
     catch(err){
       console.log(err.message);
     }
  },
  mounted(){
    
    this.Twotimer=setInterval(this.check_Twotoggle,5);
  },
  beforeDestroy(){
    clearInterval(this.Twotimer);
  }

}
</script>

<style scoped>

.page_change{
        height: 150px;
    width: 990px;
    
    margin-left: 140px;
}
.page_change_left{
  margin-left: 20px;
  float: left;
  height: 90%;
}
.page_change_right{
  margin-right: 20px;
  float: right;
  height: 90%;
}
.page_change_left :hover h2{
  opacity: 0.5;
}
.page_change_right :hover h2{
  opacity: 0.5;
}
.prev{
  margin-top: 20px;
  color: #666;
  font-size: 20px;
}
.next{
  margin-top: 20px;
  color: #666;
  font-size: 20px;
}
.page_title{
  color: rgb(199, 12, 12);
}
.author_name{
  float: right;
  padding: 12px;
}
.post_author{
 background: #f4f4f4;
 color: #666;
 border-radius: 30px  ;
 width: 15%;
     margin-left: 790px;
}
.post_footer{
  margin: 30px;
  width: 94%;
  height: 360px;
  
}
.post_link{
  margin-top: 50px;
  margin-bottom: 1em;
    padding: 20px 30px;
    word-break: break-all;
    color: #666;
    background: #f4f4f4;
    border-radius: 30px 30px 0;
}
.post_link a{
  color: red;
  text-decoration: #666;
  font-size: 15px;
}
.post_link a:hover {
  opacity: 0.5;
  
  text-decoration: red;
}
.post_body{
    height: auto;
    width: 990px;
    
    background-color: #424242;
    border-radius: 5px;
    margin-top:-150px;
    margin-left: 140px;
}
.post_card_title{
    margin-top: 50px;
    color: white;
    font-size: 32px;
    text-align: center;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.2);
}
.post_meta{
    text-align: center;
    margin: 0 0  10px;
    line-height: 14px;
    font-size: 12px;
    color: #666;
    position: relative;
    z-index: 1;
    overflow: hidden;
    margin-top: 25px;
}
.post_content{
  
  width: 960px;
  
  margin-left: 10px;
}
.post_content h1{
  color: #424242;
}
.code{
  margin-top: 40px;
  width: 960px;
}
.text_line{
      margin-top: 70px;
}
.article_label{
  width: 100%;
  height: 60px;
  
}
.label_item{
  float: left;
  margin: 10px;
}
li{
  list-style: none;
}
.a1{
  opacity: 0;
}
.b1{
  transition: all 0.5s;
}
.c1{
  opacity: 1;
}
.aa1{
  opacity: 1;
}
.bb1{
  transition: all 0.5s;
}
.cc1{
  opacity: 0;
}
.contain_header{
  width: 100%;
  height: 300px;
  background: url("../../public/image/article_bg.jpg");
  background-repeat: no-repeat;
}
#myHeader{
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
}
.container{
    margin: 0;
    padding: 0;
    width: 100%;
    height: auto;
}
</style>