<template>
  <div class="container">
    <div class="contain_header"></div>

    <m-header id="myHeader" v-show="true"></m-header>
    <div class="title">
      <h2 class="title_item">MESSAGE</h2>
    </div>
    <div class="contain">
      <div class="say">
        <div class="left_Icon">
          <h1>“</h1>
        </div>
        <div class="say_item">
          <h1>我认为世界上任何一件事情都值得全力以赴，懦夫才谈适度和中庸</h1>
        </div>
        <div class="right_Icon">
          <h1>”</h1>
        </div>
      </div>

      <div class="message">
        <el-divider></el-divider>
        <div class="message_title">
          <h2>留言板</h2>
        </div>
        <div class="message_contain">
          <el-input
            type="textarea"
            v-model="form.content"
            :rows="10"
            placeholder="留下对博主想说的话..."
          ></el-input>
        </div>
        <div class="message_footer">
          <el-button type="primary" @click="submitForm">发表</el-button>
        </div>
        <el-divider></el-divider>
      </div>

      <div class="comments">
        <div class="comments_container" v-for="(comment,index) in comments" :key="index">
          <el-container>
            <el-aside width="50px">
              <el-avatar :size="50" :src="comment.headImgUrl"></el-avatar>
            </el-aside>
            <el-container>
              <el-header>
                <el-row type="flex" justify="start">
                  <el-col class="userName" :span="5">{{comment.name}}</el-col>
                  <el-col :span="10">#{{index+1}}楼</el-col>
                  <el-col class="comment_date" :span="9">{{comment.date}}</el-col>
                </el-row>
              </el-header>
              <el-main class="item_main">{{comment.content}}</el-main>
              <el-footer>
                
                  <div class="foot_operation" @click="clickUp(comment._id)" >
                    <span class="el-icon-thumb"></span
                    >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <p>{{comment.up}}人觉得很赞</p>
                  </div>
                

                
                  <div class="foot_operation" @click="answer(comment._id)">
                    <span class="el-icon-chat-line-round"></span
                    >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <p>回复</p>
                  </div>
                
              </el-footer>
            </el-container>
          </el-container>

          <div class="comment_others" v-for="(answer,index) in comment.answers" :key="index">
            <div class="others_item">
              <el-row>
                <el-col :span="5"
                  ><span class="userName">{{answer.name}}：</span></el-col
                >
                <el-col :span="19">{{answer.content}}</el-col>
              </el-row>
              <el-row>
                
                  <div class="foot_operation">
                  
                  <p>{{answer.date}}</p>
                </div>
                
                
                
                  <div class="foot_operation" @click="answer_child(comment._id,answer._id)">
                  <span class="el-icon-chat-line-round"></span
                  >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  <p>回复</p>
                </div>
                
                
              </el-row>
              <el-divider></el-divider>
            </div>
         
            <div class="others_input" v-if="answerChildNow==answer._id">
              <el-input
                type="textarea"
                :rows="3"
                placeholder="请输入回复内容"
                v-model="answerForm.content"
              >
              </el-input>
              <div class="input_Icon">
                <el-button round @click="exitInput">取消</el-button>
                <el-button type="success" round @click="submitAnswer(comment._id)"
                  >回复</el-button
                >
              </div>
            </div>
          </div>
          <div class="others_input" v-if="answerNow==comment._id">
              <el-input
                type="textarea"
                :rows="3"
                placeholder="请输入评论内容"
                v-model="answerForm.content"
              >
              </el-input>
              <div class="input_Icon">
                <el-button round @click="exitInput">取消</el-button>
                <el-button type="success" round @click="submitAnswer(comment._id)"
                  >回复</el-button
                >
              </div>
            </div>
            <el-divider></el-divider>
        </div>
        
      </div>
    </div>

    <MFooter></MFooter>
  </div>
</template>

<script>
import MHeader from "@/commond/MHeader";
import MFooter from "@/commond/MFooter";
export default {
  components: {
    MHeader: MHeader,
    MFooter: MFooter,
  },
  inject: ["reload"],
  data() {
    return {
      answerNow:"",
      answerChildNow:"",
      comments:[],
      answerForm: {
        content: "",
        name: "",
      },
      inputIsShow_one: false,
      inputIsShow_two:false,
      // headImgUrl: require("./public/image/my_head.jpg"),
      form: {
        content: "",
        name: "",
      },
    };
  },
   created() {
    document.documentElement.scrollTop = 0;
    //获取留言数据
    this.getAllComment();
         
    
  },
  methods: {
    getAllComment(){
      const res =  this.$http.get("http://10.128.254.109:3000/api/comments").
      then(res=>{
        this.comments=(res.data);
      }) 
    },
    //点赞
    clickUp(id){
      
      if(this.$store.state.isLogin == true)
      {
        let res=this.$http.post("http://10.128.254.109:3000/api/comment/up",{
          id:id
        }).then(res=>{
          console.log(res);
           this.getAllComment()
        })
        
      }
      else{
        alert("登入后才能点赞哦")
      }
    },
    //回复留言
    answer(id) {
      
      if(this.$store.state.isLogin==true){
      this.answerNow=id;
      this.answerChildNow="";
      
      }
      else{
        alert("登入后才可以评论哦");
      }
      
    },
    //回复评论
    answer_child(answerId,answerChildId){
      if(this.$store.state.isLogin==true){
        this.answerNow='';
        this.answerChildNow=answerChildId;
      }
      else{
        alert("登入后才可以回复哦");
      }
    },
    //提交评论
    submitAnswer(id) {
      this.answerNow="";
      this.answerChildNow="";
      this.answerForm.name=JSON.parse(this.$store.state.userInfo).name;
      if(this.answerForm.content!=null){
        let form=this.answerForm
        
        this.$http.post('http://10.128.254.109:3000/api/answer',{form,id:id})
        this.getAllComment()
      }
      else{
        alert("请输入评论内容")
      }
      
     
    },
    //取消回复
    exitInput() {
      this.answerNow="";
      this.answerChildNow="";
    },
    
    //提交留言
    submitForm() {
      let userInfo=JSON.parse(this.$store.state.userInfo);
      
      this.form.name=userInfo.name;
      this.form.headImgUrl=userInfo.headImgUrl;
      if(this.$store.state.isLogin==true){
        if(this.form.content!=null){
          let res=this.$http.post('http://10.128.254.109:3000/api/comment',this.form).then(
            res=>{
console.log(res);
console.log(res.status);
if(res.status==200){
          alert("留言成功");
          this.getAllComment();
        }
            }
          )
          
        
          
        }
        else{
          alert("请输入留言内容")
        }
        }
      
      else{
        alert("登入后才可以留言哦");
      }
    },
  },
  
};
</script>

<style scoped>
