  color: grey;
}
.item_main {
  
  height: auto;
  word-wrap: break-word;
}
/* .others_item span{
  color: aqua;
} */
.others_item {
  margin-left: 3%;
  height: 75px;
  width: 96%;
  
}
.comment_others {
  height: auto;
  width: 91%;
  margin-left: 9%;
  border-left: 2px solid grey;
}
