  height: 160px;
  width: 100%;
 
}
.comments_container {
  width: 100%;
  height: auto;
  
}
.comments {
  width: 80%;
  height: auto;
  
  margin: 0 auto;
}
.message_footer {
  margin-top: 20px;
  width: 100%;
  height: 50px;
}
