  color: black;
  font-family: STXingkai;
}
.say .left_Icon {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 30px;

  margin-left: 20px;
  width: 80%;
  height: 20%;
  text-align: left;
}
.say .right_Icon {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 30px;

  margin-left: 120px;
