    res=>{
      this.userForm=res.data;
      this.newForm.number=res.data.number
    }
  )
  },
  //上传头像
  getFile(event){
    let number=JSON.parse(this.$store.state.userInfo)[0].number;
    this.file=event.target.files[0];//获取上传元素信息
    event.preventDefault();
    //只能通过formData方式传输文件
    var formData=new FormData();
    formData.append('file',this.file);
    formData.append('number',number);
    let config={
      Headers:{
        'Content-Type':'application/json'
      }
    }

    this.$http.post('http://10.128.254.109:3000/api/update/userImg',formData,config).then(
      res=>{
        if(res.data.code==0){
          alert("更换成功")
          
          this.init();
        }
      }
    )
  },
  //保存修改后的用户信息
  submitForm(userForm){
    //获取原来的名字
    let oldname=JSON.parse(this.$store.state.userInfo)[0].name;
    this.$http.post('http://10.128.254.109:3000/api/update/userInfo',{
      userInfo:userForm,
      oldname:oldname
    }).then(
      res=>{
        if(res.status==200){
          alert("修改成功");
          
          this.init();
        }
