        }
      }
    )
  },

  //修改用户密码
  updatePassword(form){
    if(form.NewPassword==form.checkNewPassword){
      this.$http.post('http://10.128.254.109:3000/api/update/password',form).then(
      res=>{
        if(res.status==200){
          alert("密码修改成功，请重新登入");
          this.$router.push({
            path:'/login'
          })
        }
        else{
          alert("密码修改失败")
        }
      }
    )
    }else{
      alert("两次输入的密码不一致，请重新输入!")
    }
    
  }


  
  
  
      
    },
 
 created(){
  
