<template>
  <div class="container">
    <div class="contain_header"></div>
      <m-header id="myHeader" ></m-header>
      <div class="title">
        <h2 class="title_item">FILES</h2>
      </div>
      <div class="content">
        <div class="left_card">
          <el-card shadow="hover" class="card_item" v-for="(article,index) in articles" :key=index>
            <el-link :underline="false" @click="checkArticle(article._id)" ><h2 >{{article.title}}</h2></el-link>
            <el-divider></el-divider>
            <div class="card_bottom">
              <el-link :underline="false" @click="checkArticle(article._id)">
                <div class="card_date">
                 <i class="el-icon-date icon_Size"></i>
                 <p class="item_text">{{article.date}}</p>
                </div>
              </el-link>
              <el-link :underline="false" href="#/tags">
              <div class="card_tags">
                <i class="el-icon-collection-tag icon_Size"></i>
                <p class="item_text">{{article.type}}</p>
              </div>
              </el-link>
            </div>
          </el-card>
          
          <div class="card_footer">
            
            <el-pagination
               
               @current-change="handleCurrentChange"
               :current-page="currentPage"
               hide-on-single-page
               :page-size="pageSize"
               layout="prev,pager,next"
               :total="totalCount"
               >
               
            </el-pagination>
                 
          </div>
        </div>
        <div class="right_card">
         <div class="right_title">
           <h2>日期</h2>
         </div>
         
         <div class="right_item" v-for="(date,index) in dateMenu" :key="index">
           <h3>{{date[1]}}年{{date[0]}}月</h3>
         </div>
        </div>
      </div>
      <MFooter></MFooter>
  </div>
</template>

<script>
import MHeader from '@/commond/MHeader';
import MFooter from '@/commond/MFooter';
export default {
components:{
    MHeader:MHeader,
    MFooter:MFooter,
    
},
data(){
  return {
    
    TwoTimer:'',
    //文章总数据
    articles:[],
    //默认显示第几页
    currentPage:1,
    //总条数，根据接口获取数据长度
    totalCount:1,
    //默认页面显示的条数（可修改）
    pageSize:8,
    //日期菜单
    dateMenu:[]
    
  }
},


//导航狼的显示和隐藏
methods:{
  //跳转文章界面
  checkArticle(id){
      
      this.$router.push({
        path:'/article/'+id
      })
    },
  //将页码，及每页显示的条数以参数传递提交给后台
  getDate(n1,n2){
    
    
      const res= this.$http.post('http://10.128.254.109:3000/api/articles/count',{
        //每页显示的条数
        PageSize:n1,
        //显示第几页
        currentPage:n2,
      }).then(res=>{
        
        this.articles=res.data.data;
        
        this.totalCount=(res.data.length-1)*10;
      })
      
  },
 
  //显示第几页
  handleCurrentChange(val){
    //改变默认的页数
    this.currentPage=val;
    //切换页码时，要获取每页显示的条数
    this.getDate(this.pageSize,val);
  },

  //生成归档月份
  monthList(){
    this.$http.get("http://10.128.254.109:3000/api/articles").then(
      res=>{
        let data=res.data;
        let month=new Array();
        let timeList=new Array()
        let dateList=new Array()
        for(let i=0;i<data.length;i++){
          month.push(data[i].date);
          timeList[i]=month[i].split("-");
          
        }
        for(let x=0;x<timeList.length;x++){
          dateList[x]=timeList[x].slice(0,2);
        }
        
        this.dateMenu=this.DelDuplication(dateList);
        
        
      }
    )
  },
  //数组去重
  DelDuplication(arr){
    
    if(arr==null||arr.length<2){
      return arr;
    }
    let res={}
    arr.forEach(item=>{
        item.sort((a,b)=>a-b);
        res[item]=item;
    });
    return Object.values(res)
    
    // let res=new Array();let temp=new Array();
    // for(let i=0;i<arr.length;i++){
     
    //   let x=arr[i][0];let y=arr[i][1];
      
    //   while(!temp[x][y]){
    //     res.push(arr[i]);
    //     temp[x][y]=true;
    //   }
    // }
    // console.log(res);
    // return res;
  }

},
async created(){
     document.documentElement.scrollTop=0;
     this.getDate(this.pageSize,this.currentPage)
     
     this.monthList()
    
  },




}
</script>

