<template>
  <el-menu
    
    class="el-menu-demo "
    mode="horizontal"
    
    background-color="#545c64"
    text-color="#fff"
    
  >
    
        
    <el-menu-item >
        <el-link :underline="false" href="/">
        <el-avatar shape="square" :size="40" :src="squareUrl"></el-avatar>&nbsp;
        <span>守夜人</span>
        </el-link>
    </el-menu-item>
        
    <el-menu-item index="1">
        <el-link :underline="false" href="#/home">
        <span><i class="el-icon-s-home"></i>首页</span>
        </el-link>
    </el-menu-item>

    <el-menu-item index="2">
        <el-link :underline="false" href="#/tags">
        <span><i class="el-icon-location-information"></i>标签</span>
        </el-link>
    </el-menu-item>
    <el-menu-item index="3">
        <el-link :underline="false" href="#/file">
        <span><i class="el-icon-date"></i>归档</span>
        </el-link>
    </el-menu-item>
    <el-menu-item index="4">
        <el-link :underline="false" href="#/message">
        <span><i class="el-icon-edit"></i>留言</span>
        </el-link>
    </el-menu-item>
    <div class="right">
    <el-menu-item class="right_login"  v-show="isLoginShow">
        <el-link :underline="false" href="#/login" >
        <el-button type="primary" plain>登入</el-button>
        </el-link>
    </el-menu-item>
    <el-menu-item  class="right_register"  v-show="isLoginShow">
        <el-link :underline="false" href="#/register" target="_blank">
        <el-button type="success" plain>注册</el-button>
        </el-link>
    </el-menu-item>
    <el-menu-item  class="right_user" v-show="isUserShow">
        
        <el-avatar shape="square" :size="40" :src="userHeadImg"></el-avatar>&nbsp;
         <el-dropdown trigger="click" @command="handleCommand">
      <span class="el-dropdown-link">
        {{userName}}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown" >
        <el-link :underline="false" href="#/administer"><el-dropdown-item  icon="el-icon-s-check">个人信息</el-dropdown-item></el-link>
        <el-link :underline="false" href="#/home">
<el-dropdown-item icon="el-icon-close" command="exit">
  
  退出登入
  </el-dropdown-item>
        </el-link>

        
        
        
      </el-dropdown-menu>
    </el-dropdown>
        

    </el-menu-item>
    </div>
  </el-menu>

</template>

<script>
export default {
  inject: ["reload"],
  data() {
    return {
      isUserShow:false,
      isLoginShow:true,
     
      squareUrl:"http://10.128.254.109:3000/img/my_head.jpg",
      userName:'',
      userHeadImg:''
    };
  },
  methods: {
    handleCommand(command){
      if(command=='exit'){
        alert("退出登入")
        this.$store.commit('ISEXIT');
      }
    }
    
  },
  created(){
    
      let isLogin=this.$store.state.isLogin;
      
      let userInfo=JSON.parse(this.$store.state.userInfo);
      
      
      if(isLogin===true){
        this.isUserShow=true;
        this.isLoginShow=false;
        this.$http.post('http://10.128.254.109:3000/api/users/findOne',{number:userInfo.number}).then(
          res=>{
            
            this.userName=res.data.name;
            this.userHeadImg=res.data.headImgUrl;
          }
        )


        
        
      }
      else{
        this.isUserShow=false;
        this.isLoginShow=true;
      }
    
    
  }
  
};
</script>

<style scoped>
.el-menu-demo{
  z-index: 10;
}
.right{
    float:right;
}
.right_login{
    display: inline-block;
}
.right_register{
display: inline-block;
}
.right_user{
  display: inline-block;
  
  margin-right: 50px;
  
}
.right_user span{
  color: aliceblue;
}

</style>
