<template>
  <div>
      <transition appear
                    enter-class="a1"
                    enter-active-class="b1"
                    enter-to-class="c1"
                    leave-class="aa1"
                    leave-active-class="bb1"
                    leave-to-class="cc1"
                    >
      <m-header id="myHeader" v-show="Header_IsShow"></m-header>
      </transition>
      <header class="contain_header">
        <div class="contain_header_title">
          <h1 class="header_title">守夜人'BLOG</h1>
          <h5 class="header_text">
            <p>身处黑暗，向往光明————</p>
          </h5>
          
        </div>
        <transition appear
                    enter-class="a"
                    enter-active-class="b"
                    enter-to-class="c"
                    leave-class="aa"
                    leave-active-class="bb"
                    leave-to-class="cc"
                    >
            <div class="my-el-icon-arrow-down" v-show="Header_Icon_IsShow">
                <i class="el-icon-arrow-down " @click="IconClick" >
                </i>
            </div>
        </transition>
        
      </header>
      <!--导航-->
      
      <router-view :key="this.$router.path"></router-view>
      
      <MFooter></MFooter>
  </div>
</template>

<script>
import MHeader from '@/commond/MHeader';
import MFooter from '@/commond/MFooter';
export default {
  components:{
    MHeader:MHeader,
    MFooter:MFooter
  },
  inject: ["reload"],
  data(){
    return {
      Header_Icon_IsShow:false,
      Header_IsShow:false,
      Onetimer:'',
      Twotimer:'',
    }
  }
  ,
  methods:{
    //设置头部图标浮动动画
    Onetoggle(){
      this.Header_Icon_IsShow=!this.Header_Icon_IsShow;
    },
    //导航隐藏和显示动画
    Twotoggle(){
      this.Header_IsShow=!this.Header_IsShow;
    },
    check_Twotoggle(){
      if(document.documentElement.scrollTop<667){
        this.Header_IsShow=false;
      }
      else{
        this.Header_IsShow=true;
      }
    },
    IconClick(){
      document.documentElement.scrollTop=668;
    },
   
  },
  created(){
    // var res=this.$http.get('/apis/')
    // console.log(res);
  },
  mounted(){
    
    this.Onetimer=setInterval(this.Onetoggle,1000);
    this.Twotimer=setInterval(this.check_Twotoggle,5);
    
    
  },
  beforeDestroy(){
    clearInterval(this.Onetimer);
    clearInterval(this.Twotimer);
  }


}
</script>

<style scpoed>
.el-icon-arrow-down{
  cursor:pointer;
}
/*  */
#myHeader{
  position: fixed;
  width: 100%;
  z-index: 2;
}
.a{
  padding-top: 380px;
  opacity: 0.5;
}
.b{
  transition: all 1s;
}
.c{
   opacity: 1;
   padding-top: 400px;

}
.aa{
  padding-top: 400px;
  opacity: 1;
}
.bb{
  transition: all 1s;
}
.cc{
   opacity: 0.5;
   padding-top: 380px;

} 
.a1{
  opacity: 0;
}
.b1{
  transition: all 0.5s;
}
.c1{
  opacity: 1;
}
.aa1{
  opacity: 1;
}
.bb1{
  transition: all 0.5s;
}
.cc1{
  opacity: 0;
}
.contain_header{
  width: 100%;
      height: 728px;
  background: url("../public/image/login_bg.jpg");
}
.contain_header_title{
  
  height: 50px;
  width: 700px;
  padding-left: 540px;
  padding-top: 184px;
  color: aqua;
  font-family:STXingkai;
}
.header_title{
  font-size: 70px;
}
.header_text{
  padding-top: 35px;
  font-size: 30px;
}
.my-el-icon-arrow-down{
  display: inline-block;
  color: aqua;
  font-size: 40px;
  padding-left: 728px;
}

</style>